import React, { useState } from "react";
import bestData from "assets/data/bestData.json";
import BestItemComponent from "components/BestItemComponent";

const BestLayout: React.FC = () => {
  const [selectedDiv, setSelectedDiv] = useState<string | null>("ALL");

  const handleButtonClick = (divName: string) => {
    setSelectedDiv(divName);
  };

  const [brandsearch, setBrandSearch] = useState("");
  const handleSearching = () => {
    console.log(brandsearch);
  };

  let [isInputClicked, setIsInputClicked] = useState(false);

  const menuList = ["ALL", "WOMEN", "MEN", "LIFE", "BEAUTY"];

  return (
    <div className="best-container">
      <h2 className="sub-title">BEST</h2>
      <div className="event-search">
        <div className="search-container">
          <div className="search-area">
            <div className="flex align-center">
              <input
                type="text"
                onFocus={() => {
                  setIsInputClicked(true);
                }}
                //     input    )
                onBlur={() => {
                  setIsInputClicked(false);
                }}
                placeholder={
                  isInputClicked === true ? "" : "  ,  "
                }
                onChange={(e) => setBrandSearch(e.target.value)}
              />
              <button
                className="icon search-icon"
                onClick={() => handleSearching()}
              ></button>
            </div>
          </div>
        </div>
      </div>
      <ul className="event-tab">
        {menuList.map((value) => (
          <li data-hashtag={{ value }} key={value}>
            <button
              className={selectedDiv === value ? "selected" : ""}
              onClick={() => handleButtonClick(value)}
              type="button"
            >
              {value}
            </button>
          </li>
        ))}
      </ul>

      {selectedDiv === "ALL" && <BestItemComponent datalist={bestData} />}
      {selectedDiv === "WOMEN" && (
        <div className="section-wrap">
          <div className="section section-1">
            <BestItemComponent
              datalist={bestData}
              category={"WOMEN"}
              id={[1, 2, 3]}
            />
          </div>
          <div className="section section-2">
            <BestItemComponent
              datalist={bestData}
              category={"WOMEN"}
              id={[4, 5, 6, 7]}
            />
          </div>
        </div>
      )}

      {selectedDiv === "MEN" && (
        <div className="section-wrap">
          <div className="section section-1">
            <BestItemComponent
              datalist={bestData}
              category={"MEN"}
              id={[1, 2, 3]}
            />
          </div>
          <div className="section section-2">
            <BestItemComponent
              datalist={bestData}
              category={"MEN"}
              id={[4, 5, 6, 7]}
            />
          </div>
        </div>
      )}
      {selectedDiv === "BEAUTY" && (
        <div className="section-wrap">
          <div className="section section-1">
            <BestItemComponent
              datalist={bestData}
              category={"BEAUTY"}
              id={[1, 2, 3]}
            />
          </div>
          <div className="section section-2">
            <BestItemComponent
              datalist={bestData}
              category={"BEAUTY"}
              id={[4, 5, 6, 7]}
            />
          </div>
        </div>
      )}
      {selectedDiv === "LIFE" && (
        <div className="section-wrap">
          <div className="section section-1">
            <BestItemComponent
              datalist={bestData}
              category={"LIFE"}
              id={[1, 2, 3]}
            />
          </div>
          <div className="section section-2">
            <BestItemComponent
              datalist={bestData}
              category={"LIFE"}
              id={[4, 5, 6, 7]}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default BestLayout;
