import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const DesignerCarousel: React.FC = () => {
  type slideData = {
    id: number;
    url: string;
  };
  const [carousels, setCarousels] = useState<slideData[]>([]);

  // USEEFFECT , []   ,      
  useEffect(() => {
    fetch("/data/designerCarousel.json")
      .then((res) => res.json())
      .then((data) => {
        console.log("ok");
        setCarousels(data);
      });
    return () => {
      console.log("unmount     ");
    };
  }, []);

  var settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    speed: 500,
    slidesToShow: 2,
    slideToScroll: 1,
  };

  return (
    <div className="designer-carousel">
      <Slider {...settings}>
        {carousels!.map((carousel) => (
          <li key={carousel.id}>
            <img src={carousel.url} alt="test" />
          </li>
        ))}
      </Slider>
    </div>
  );
};

export default DesignerCarousel;
